var graceChecker = require('@/common/graceChecker.js');
import pwInput from '@/components/input/PwInput'
export default {
	components: {
		pwInput
	},
	data() {
		return {
			tel: '',
			code: '',
			passWord: '',
			passWorda: '',
			payWord: '',
			nvitationCode: '', //邀请码
			second: 90,
			showText: true
		};
	},
	onLoad(option) {
		if (option.nvitationCode) this.nvitationCode = parseInt(option.nvitationCode);
	},
	methods: {
		sendCode() {
			//发送验证码
			var _this = this;
			if (_this.tel == '') {
				_this.$tools.toast('请填写正确的手机号码')
				return false;
			}

			//发送验证码
			_this.$request.common
				.sendCode({
					phone: _this.tel,
					type: 2
				})
				.then(data => {
					_this.$tools.loadingHide();
					if (data.status == 200) {
						_this.showText = false;
						var interval = setInterval(() => {
							let times = --_this.second;
							_this.second = times < 10 ? '0' + times : times; //小于10秒补 0
						}, 1000);
						setTimeout(() => {
							clearInterval(interval);
							_this.second = 90;
							_this.showText = true;
						}, 90000);
					} else {
						_this.$tools.toast(data.msg)
					}
				})
				.catch(err => {
					_this.$tools.loadingHide();
					//消息异常
					_this.$tools.toast('数据加载异常')
				});
		},
		btnRegister() {
			var _this = this;
			//定义表单规则
			var rule = [{
					name: 'userName',
					checkType: 'phoneno',
					errorMsg: '电话号码不正确'
				},
				{
					name: 'code',
					checkType: 'string',
					checkRule: '6,6',
					errorMsg: '验证码6个字符'
				},
				{
					name: 'pawd',
					checkType: 'string',
					checkRule: '6,20',
					errorMsg: '密码为6-20个字符'
				},
				{
					name: 'pay_paw',
					checkType: 'string',
					checkRule: '6,6',
					errorMsg: '支付密码为6个字符'
				},
				{
					name: 'invitation',
					checkType: 'string',
					checkRule: '6,6',
					errorMsg: '邀请码为6个字符'
				},
			];
			console.log(_this.$refs['userPwd'].getPassWord(), _this.$refs['passWorda'].getPassWord())
			if(_this.$refs['userPwd'].getPassWord() != _this.$refs['passWorda'].getPassWord()){
				this.$tools.toast('两次输入的密码不一致!')
				return;
			}
			let formData = {
				userName: _this.tel,
				code: _this.code,
				pawd: _this.$refs['userPwd'].getPassWord(),
				pay_paw: _this.$refs['payWord'].getPassWord(),
				invitation: _this.nvitationCode
			}
			var checkRes = graceChecker.check(formData, rule);
			if (checkRes) {
				_this.$tools.loading('数据提交中')
				_this.$request.user
					.register(formData)
					.then(data => {
						_this.$tools.loadingHide();
						if (data.status == 200) {
							_this.$tools.toast('注册成功!')
							_this.$tools.loading('前往登录中...')
							setTimeout(()=>{
								_this.$tools.loadingHide();
								uni.navigateTo({
									url: '/pages/member/login'
								});
								
							},500)	
							
						} else {
							_this.$tools.toast(data.msg)
						}
					})
					.catch(err => {
						_this.$tools.loadingHide();
						//消息异常
						_this.$tools.toast('数据加载异常')
					});
			} else {
				_this.$tools.toast(graceChecker.error)
			}
		}
	}
};
